import AdminPage from './components/pages/AdminPage';
import UserManagementPage from './components/admin/UserManagementPage';

// Simple Login Component
const SimpleLoginPage = () => {
  const handleLogin = (e) => {
    e.preventDefault();
    const formData = new FormData(e.target);
    const email = formData.get('email');
    const password = formData.get('password');

    // Mock user database
    const users = {
      'admin@troupedev.com': { password: 'admin123', role: 'admin', name: 'Admin User' },
      'student@troupedev.com': { password: 'student123', role: 'student', name: 'John Student' },
      'tutor@troupedev.com': { password: 'tutor123', role: 'tutor', name: 'Jane Tutor' },
      'guardian@troupedev.com': { password: 'guardian123', role: 'guardian', name: 'Parent Guardian' }
    };

    // Validate credentials
    const user = users[email];
    if (user && user.password === password) {
      // Successful login
      console.log('Login successful:', { email, role: user.role });
      localStorage.setItem('auth_token', 'mock_token_' + Date.now());
      localStorage.setItem('user_role', user.role);
      localStorage.setItem('user_name', user.name);
      localStorage.setItem('user_email', email);

      // Show success message and redirect based on role
      alert(`Login successful! Welcome ${user.name} (${user.role}).`);

      // Route admins to admin portal, others to regular dashboard
      if (user.role === 'admin') {
        window.location.href = '/admin';
      } else {
        window.location.href = '/dashboard';
      }
    } else {
      // Failed login
      alert('Invalid email or password. Please check your credentials.');
    }
  };

  return (
    <div style={{
      minHeight: '100vh',
      display: 'flex',
      alignItems: 'center',
      justifyContent: 'center',
      background: 'linear-gradient(135deg, #667eea 0%, #764ba2 100%)',
      padding: '1rem'
    }}>
      <div style={{
        background: 'white',
        padding: '2rem',
        borderRadius: '8px',
        boxShadow: '0 4px 12px rgba(0,0,0,0.15)',
        width: '100%',
        maxWidth: '400px'
      }}>
        <h1 style={{ textAlign: 'center', marginBottom: '2rem', color: '#1a1a1a' }}>
          TroupeDev Login
        </h1>
        <form onSubmit={handleLogin}>
          <div style={{ marginBottom: '1rem' }}>
            <label style={{ display: 'block', marginBottom: '0.5rem', fontWeight: '500' }}>
              Email
            </label>
            <input
              type="email"
              name="email"
              required
              defaultValue="admin@troupedev.com"
              style={{
                width: '100%',
                padding: '0.75rem',
                border: '1px solid #ddd',
                borderRadius: '4px',
                fontSize: '1rem'
              }}
            />
          </div>
          <div style={{ marginBottom: '1.5rem' }}>
            <label style={{ display: 'block', marginBottom: '0.5rem', fontWeight: '500' }}>
              Password
            </label>
            <input
              type="password"
              name="password"
              required
              defaultValue="admin123"
              style={{
                width: '100%',
                padding: '0.75rem',
                border: '1px solid #ddd',
                borderRadius: '4px',
                fontSize: '1rem'
              }}
            />
          </div>
          <button
            type="submit"
            style={{
              width: '100%',
              padding: '0.75rem',
              backgroundColor: '#0066cc',
              color: 'white',
              border: 'none',
              borderRadius: '4px',
              fontSize: '1rem',
              fontWeight: '500',
              cursor: 'pointer'
            }}
          >
            Sign In
          </button>
        </form>
        <div style={{
          marginTop: '1rem',
          padding: '1rem',
          backgroundColor: '#f8f9fa',
          borderRadius: '4px',
          fontSize: '0.875rem'
        }}>
          <strong>Demo Credentials:</strong><br />
          <div style={{ marginTop: '0.5rem' }}>
            <strong>Admin:</strong> admin@troupedev.com / admin123<br />
            <strong>Student:</strong> student@troupedev.com / student123<br />
            <strong>Tutor:</strong> tutor@troupedev.com / tutor123<br />
            <strong>Guardian:</strong> guardian@troupedev.com / guardian123
          </div>
        </div>
      </div>
    </div>
  );
};

// Simple Dashboard Component
