/**
 * EmptyState Component
 * A component for displaying empty states with optional actions
 */

import React from 'react';
import PropTypes from 'prop-types';
import styled from 'styled-components';
import Button from './Button';

const EmptyStateContainer = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  padding: 3rem 2rem;
  min-height: ${props => props.minHeight || '200px'};
  max-width: 500px;
  margin: 0 auto;
`;

const EmptyStateIcon = styled.div`
  font-size: 3rem;
  margin-bottom: 1.5rem;
  opacity: 0.8;
  line-height: 1;

  /* If it's a text icon (emoji), ensure proper rendering */
  font-family: 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol', 'Noto Color Emoji', sans-serif;

  /* If it's an SVG or image */
  img, svg {
    width: 3rem;
    height: 3rem;
    opacity: 0.6;
  }
`;

const EmptyStateTitle = styled.h3`
  font-size: 1.25rem;
  font-weight: 600;
  color: var(--color-text-primary, #1a1a1a);
  margin: 0 0 0.75rem 0;
  line-height: 1.4;
`;

const EmptyStateDescription = styled.p`
  font-size: 1rem;
  color: var(--color-text-secondary, #6c757d);
  margin: 0 0 2rem 0;
  line-height: 1.5;
  max-width: 400px;
`;

const EmptyStateActions = styled.div`
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
  align-items: center;

  @media (min-width: 480px) {
    flex-direction: row;
    gap: 1rem;
  }
`;

const EmptyState = ({
  icon,
  title,
  description,
  action,
  secondaryAction,
  className = '',
  style = {},
  minHeight,
  children,
  ...props
}) => {
  const renderIcon = () => {
    if (!icon) return null;

    if (typeof icon === 'string') {
      // Assume it's an emoji or text
      return <EmptyStateIcon>{icon}</EmptyStateIcon>;
    }

    // Assume it's a React component/element
    return <EmptyStateIcon>{icon}</EmptyStateIcon>;
  };

  const renderAction = (actionConfig, isPrimary = true) => {
    if (!actionConfig) return null;

    if (typeof actionConfig === 'function') {
      return actionConfig();
    }

    if (React.isValidElement(actionConfig)) {
      return actionConfig;
    }

    const {
      text,
      onClick,
      variant = isPrimary ? 'primary' : 'outline-primary',
      size = 'medium',
      disabled = false,
      ...actionProps
    } = actionConfig;

    return (
      <Button
        variant={variant}
        size={size}
        onClick={onClick}
        disabled={disabled}
        {...actionProps}
      >
        {text}
      </Button>
    );
  };

  return (
    <EmptyStateContainer
      className={`empty-state ${className}`}
      style={style}
      minHeight={minHeight}
      {...props}
    >
      {renderIcon()}

      {title && (
        <EmptyStateTitle>{title}</EmptyStateTitle>
      )}

      {description && (
        <EmptyStateDescription>{description}</EmptyStateDescription>
      )}

      {children && (
        <div className="empty-state-custom-content">
          {children}
        </div>
      )}

      {(action || secondaryAction) && (
        <EmptyStateActions>
          {renderAction(action, true)}
          {renderAction(secondaryAction, false)}
        </EmptyStateActions>
      )}
    </EmptyStateContainer>
  );
};

EmptyState.propTypes = {
  icon: PropTypes.oneOfType([PropTypes.string, PropTypes.node]),
  title: PropTypes.string,
  description: PropTypes.string,
  action: PropTypes.oneOfType([
    PropTypes.shape({
      text: PropTypes.string.isRequired,
      onClick: PropTypes.func.isRequired,
      variant: PropTypes.string,
      size: PropTypes.string,
      disabled: PropTypes.bool
    }),
    PropTypes.func,
    PropTypes.node
  ]),
  secondaryAction: PropTypes.oneOfType([
    PropTypes.shape({
      text: PropTypes.string.isRequired,
      onClick: PropTypes.func.isRequired,
      variant: PropTypes.string,
      size: PropTypes.string,
      disabled: PropTypes.bool
    }),
    PropTypes.func,
    PropTypes.node
  ]),
  className: PropTypes.string,
  style: PropTypes.object,
  minHeight: PropTypes.string,
  children: PropTypes.node
};

// Predefined empty state variants for common use cases
